export default {
	data() {
		return {
			title: '',
			nodes: ''
		}
	},
	onLoad(params) {
		let _this = this;
		_this.request({
			url: _this.http.getNewsDetail,
			data: {
				newsId: params.newsId
			},
			success: (res) => {
				if (res.code === '0') {
					_this.title = res.result.newsTitle;
					_this.nodes = res.result.newsContent;
				}
			}
		});
	},
	methods: {

	}
}
